<template>
  <div>
    <md-field>
      <label>Search Name</label>
      <md-input v-model="searchStaff.username"></md-input>
    </md-field>
    <md-button class="md-success" @click="selectByuserName(searchStaff.username)"> Search</md-button>
    <md-button class="md-warning" @click="clear">Clear</md-button>
    <md-table v-model="tableData" :table-header-color="tableHeaderColor">
      <md-table-row slot="md-table-row" slot-scope="{ item }">
        <md-table-cell md-label="Avatar">{{ item.userid }}</md-table-cell>
        <md-table-cell md-label="FirstName">{{ item.firstname }}</md-table-cell>
        <md-table-cell md-label="LastName">{{ item.lastname}}</md-table-cell>
        <md-button class="md-success"><router-link :to="{
          name:'User Profile',
          params:{
		         username:item.username,
             password:item.password,
             firstname:item.firstname,
             lastname:item.lastname,
             telephone:item.telephone,
             email:item.email,
		}
        }">Look More</router-link></md-button>

        <md-button class="md-danger" @click="deleteByuserName(item)">Delete</md-button>
      </md-table-row>
    </md-table>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "simple-table",
  props: {
    tableHeaderColor: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      dialogTableVisible: false,
      dialogFormVisible: false, // 详情表单
      dialogVisible: false,   // 删除对话框
      formLabelWidth: '120px',
      pageSize: 10, // 每页显示10条
      totalCount: 100, // 总记录数初始化为100
      currentPage: 1, // 当前页码
      tableData: [
        {userid:'001',firstname: 'jack',lastname: 'jon',username:'1234',email:'1',telephone:'213',password:'123'},
        {userid:'002',firstname: 'Tom',lastname: 'TIm',username:'1234',email:'1',telephone:'213',password:'123'},

      ], // 接收返回的数据
      deleteName: "", // 删除的username名称
      form: { // 用来显示详情数据和更新数据
        Avatar: "",
        userName: "",
        passWord: "",
        firstName: "",
        lastName: "",
        telephone: "",
        email: ""
      },
      searchStaff: { // 接收输入的username
        username: ""
      },
      staff: {
        image: ""
      }
    }
  },

      // selected: [],
      // users: [
      //   {
      //     name: "Dakota Rice",
      //     salary: "$36,738",
      //     country: "Niger",
      //     city: "Oud-Turnhout",
      //   },
      //   {
      //     name: "Minerva Hooper",
      //     salary: "$23,738",
      //     country: "Curaçao",
      //     city: "Sinaai-Waas",
      //   },
      //   {
      //     name: "Sage Rodriguez",
      //     salary: "$56,142",
      //     country: "Netherlands",
      //     city: "Overland Park",
      //   },
      //   {
      //     name: "Philip Chaney",
      //     salary: "$38,735",
      //     country: "Korea, South",
      //     city: "Gloucester",
      //   },
      //   {
      //     name: "Doris Greene",
      //     salary: "$63,542",
      //     country: "Malawi",
      //     city: "Feldkirchen in Kārnten",
      //   },
      //   {
      //     name: "Mason Porter",
      //     salary: "$78,615",
      //     country: "Chile",
      //     city: "Gloucester",
      //   },
      // ],
  mounted() { // 页面挂载完毕，请求全部数据，记录数组长度，利于分页
    let that = this;
    axios.get("http://localhost:8080/admin/staff")
        .then(function (resp) {
          if (resp.data.code === 1) {
            that.tableData = resp.data.data;
          } else {
            alert(resp.data.msg);
          }

        })
  },
  methods:{
    selectByuserName(userName) {
      let that = this;
      axios.get("http://localhost:8080/admin/staff/" + userName).then(function (resp) {
        if (resp.data.code === 1) {
          that.tableData = resp.data.data;
        } else {
          alert(resp.data.msg);
        }
      })
    },
    clear() {
      this.searchStaff.username = ''
    },
    deleteByuserName(item) {
      this.deleteName=item.username;
      let that = this;
      alert("删除成功");
      console.log(that.deleteName);
      axios.delete("http://localhost:8080/admin/staff/" + that.deleteName)
          .then(function (resp) {
            if (resp.data.code === 1) {
              axios.get("http://localhost:8080/admin/staff")
                  .then(function (response) {
                    if (response.data.code === 1) {
                      that.tableData = response.data.data;
                    } else {
                      alert(response.data.msg);
                    }

                  })
              that.reload();
            } else {
              alert(resp.data.msg);
            }

            // that.tableData = resp.data;
            // that.reload();
          })
    },
  }


};
</script>
